import uPopup from '../../basic-component/u-popup/u-popup.vue';

export default {
    name: "bd-xbc",
    props: {
        attrGroups: Array,
        services: Array,
        attrList: Array,
        guaranteeTitle: String,
        guaranteePic: String,
        param_content: {
            type: Array,
            default: function() {
                return [];
            }
        },
        param_name: String,
        type: String,
        goodsStock: Number,
        coAttr: {
            type: [Number, Boolean],
            default: 1
        }
    },
    data() {
        return {
            attrStr: `请选择规格`,
            serviceStr: ``,
            paramStr: ``,
            newAttrGroups: [],
            attrCount: [],
            attrName: '',
            pictureList: [],
            show: false,
            isShow: '',
        }
    },
    watch: {
        attrList: {
            deep: true,
            immediate: true,
            handler: function(newVal) {
                let str = ``;
                if (!this.$validation.isEmpty(newVal)) {
                    newVal.forEach(item => {
                        str += `${item.attr_group_name}:${item.attr_name} `;
                    });
                } else {
                    str = '请选择规格';
                }
                this.attrStr = str;
            }
        },
        services: {
            immediate: true,
            handler: function() {
                let str = ``;
                if (!this.$validation.isEmpty(this.services)) {
                    this.services.forEach(item => {
                        str += `${item.name} · `;
                    });
                }
                str = str.substring(0, str.lastIndexOf(' · ') + 1);
                this.serviceStr = str;
            }
        },
        attrGroups: {
            immediate: true,
            handler(newVal) {
                this.newAttrGroups = [];
                this.pictureList = [];
                this.attrCount = 0;
                this.attrName = '';
                if(newVal && newVal.length > 0) {
                    newVal.forEach((item, index) => {
                        if (!this.attrName) {
                            this.attrName = item.attr_group_name;
                            item.attr_list.forEach( () => {
                                this.attrCount += 1;
                            });
                        }
                        if (index === 0) {
                            item.attr_list.forEach(item2 => {
                                if (this.newAttrGroups.length < 3) this.newAttrGroups.push(item2);
                                if (item2.pic_url && this.pictureList.length < 5) this.pictureList.push(item2.pic_url);
                            });
                        }
                    });
                }
            }
        },
        param_content: {
            immediate: true,
            handler: function(newVal) {
                let str = ``;
                if (!this.$validation.isEmpty(newVal)) {
                    newVal.forEach(item => {
                        str += `${item.key} `;
                    });
                }
                str = str.substring(0, str.lastIndexOf(' ') + 1);
                this.paramStr = str;
            }
        }
    },
    components: {
        uPopup
    },
    methods: {
        open(data) {
            if (data === 'attr') {
                this.$emit('openAttr');
            } else {
                this.show = true;
                this.isShow = data;
            }
        }
    }
}
